<template>
  <div class="container">
    <good-detail-tab v-if="!isSociology" :tabs="tabs" @switch="switchTab"></good-detail-tab>
    <div class="goods-detail">
      <lazy-component>
        <div class="goods-detail-info">
          <div class="goods-detail-info-specifications" v-show="productAttrs && productAttrs.length > 0 && (isSociology||currentTab==='specifications')">
            <ul>
              <li :key="index" v-for="(item,index) in productAttrs" :class="[isSociology&&(index > 3) ? 'overflow-attrs' : '', extendAttr ? '__extended' : '']">
                <div class="left" v-html="item.attrName.replace('（','<br>（').replace('(','<br>(')"></div>
                <div class="right">{{item.attrValue}}</div>
              </li>
            </ul>
            <div v-show="isSociology&&(productAttrs.length > 4)" @click="changeExtendStatus" class="extend-btn">
              {{extendAttr ? '收起' : '展开'}}
              <img :src="extendAttr ? extendIcon.notExtend: extendIcon.extend" />
            </div>
          </div>
          <div v-show="isSociology||currentTab==='detail'" class="goods-detail-info-detail">
            <video-player :playsinline="true" :options="playerOptions" v-if="mp4FileId2" />
            <img :src="sgTopPic" alt v-if="sgTopPic" />
            <p class="richText" v-html="richTextResult" v-show="richText&&isSociology"></p>
            <p class="richText" v-html="productDetailsResult" v-show="productDetails&&!isSociology"></p>
            <img :src="sgBottomPic | imageToHttps" alt v-if="sgBottomPic" />
          </div>
          <div v-show="!isSociology&&currentTab==='aftersale'" class="goods-detail-aftersale">
            <p class="richText" v-html="afterSales"></p>
          </div>
        </div>
      </lazy-component>
      <!-- 猜你喜欢 -->
      <guess-you-lick :more='true'/>
    </div>
  </div>
</template>

<script>
import 'video.js/dist/video-js.css'
import { videoPlayer } from 'vue-video-player'
import { handleImgSrc } from '@/utils/filters'
import { ImagePreview } from 'vant';
import guessYouLick from '@/components/guessYouLick'
const goodDetailTab = () => import('./good-detail-tab/good-detail-tab')
let prevImgs

const tabScrollHeight = {
  detail: 0,
  specifications: 0,
  aftersale: 0
}

export default {
  components: {
    videoPlayer,
    guessYouLick,
    goodDetailTab
  },
  props: ['mp4FileId2', 'sgTopPic', 'products', 'richText', 'productDetails', 'sgBottomPic', 'productAttrs', 'afterSales'],
  watch: {
    afterSales: {
      handler(val) {
        val && (this.tabs.aftersale = true)
      },
      immediate: true
    },
    sgTopPic: {
      handler(val) {
        val && (this.tabs.detail = true)
      },
      immediate: true
    },
    productDetails: {
      handler(val) {
        val && (this.tabs.detail = true)
      },
      immediate: true
    },
    productAttrs: {
      handler(val) {
        if (val && val.length) {
          this.tabs.specifications = true
        }
      },
      immediate: true
    },
    products: {
      handler (val) {
        if (val) {
          this.isSociology = !(val.productAttribute === 0)
          if (this.isSociology) {
            this.currentTab = ''
          }
        }
      },
      immediate: true
    }
  },
  data() {
    return {
      extendAttr: false, // 是否已展开规格
      extendIcon: {
        extend: '//cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goodsDetail/down.png',
        notExtend: '//cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goodsDetail/up.png'
      },
      prevInstance: null,
      // detailFlag: true,
      mp4FileId2_json: {},
      tabs: {
        detail: false,
        specifications: false,
        aftersale: false
      },
      currentTab: '',
      isSociology: true
    }
  },
  computed: {
    playerOptions() {
      const json = this.$util.strJSONParse(this.mp4FileId2, {});
      const poster = json.img ? json.img.replace('http:', 'https:') : '';
      const src = json.video ? json.video.replace('http:', 'https:') : '';
      return {
        playbackRates: [0.7, 1.0, 1.5, 2.0], // 播放速度
        autoplay: false, // 如果true,浏览器准备好时开始回放。
        muted: false, // 默认情况下将会消除任何音频。
        loop: false, // 导致视频一结束就重新开始。
        preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: 'zh-CN',
        aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        sources: [
          {
            // type: '', // 这里的种类支持很多种：基本视频格式、直播、流媒体等，具体可以参看git网址项目
            src: src, // url地址
          },
        ],
        poster: poster, // 你的封面地址
        // width: document.documentElement.clientWidth, //播放器宽度
        notSupportedMessage: '此视频暂无法播放，请稍后再试', // 允许覆盖Video.js无法播放媒体源时显示的默认信息。
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true, // 全屏按钮
        },
      }
    },
    richTextResult () {
      let [result, arr] = handleImgSrc(this.richText, this.$webpSurpport);
      prevImgs = arr
      return this.addEvent(result)
    },
    productDetailsResult() {
      let [result, arr] = handleImgSrc(this.productDetails, this.$webpSurpport)
      prevImgs = arr
      return this.addEvent(result)
    },
  },
  methods: {
    switchTab (e) {
      this.handleScrollHeight(this.currentTab, e)
      this.currentTab = e
    },
    handleScrollHeight (oldTab, newTab) {
      const goodsDetail = document.querySelector('.goods-detail')
      const innerView = document.querySelector('.inner-view')
      if (innerView && goodsDetail) {
        if (goodsDetail.getBoundingClientRect().top < 100) {
          tabScrollHeight[oldTab] = innerView.scrollTop
        } else {
          if (oldTab !== 'specifications') {
            tabScrollHeight[oldTab] = 0
            tabScrollHeight[newTab] = 0
          }
        }
        if (tabScrollHeight[newTab]) {
          this.$nextTick(() => {
            innerView.scrollTo(0, tabScrollHeight[newTab])
          })
        } else {
          if (goodsDetail.getBoundingClientRect().top < 100) {
            innerView.scrollTo(0, goodsDetail.offsetTop - 100)
          }
        }
      }
    },
    changeExtendStatus() {
      this.extendAttr = !this.extendAttr
    },
    addEvent(result) {
      let index = -1
      return result.replace(/<img[^<]*?/gi, () => {
        index += 1
        return `<img data-index="${index}" onclick="_imgPreviewFn(this)"`
      })
    },
    handleImgClick(context) {
      this.prevInstance = ImagePreview({
        images: prevImgs,
        startPosition: +context.dataset.index
      })
    },
    // fnChangeDetailFlag(flag) {
    //   this.detailFlag = flag
    // },
  },
  created() {
    window._imgPreviewFn = this.handleImgClick
  },

  beforeDestroy() {
    if (this.prevInstance) this.prevInstance.close()
    window._imgPreviewFn = null
  }
}
</script>
